/**
 * @jsx React.DOM
 */

define('js/components/mixins/github_error_handler',[
  "react",
  "js/utils",
  "jquery",
  "js/components/generic/modal"
],function (
  React,
  Utils,
  $,
  Modal
) {
    'use strict';

    var GithubErrorHandler = {

        renderErrorPage : function(errorData){
            for(var resource in errorData){
                var data = errorData[resource];
                var remainingRequests = data.getResponseHeader('X-RateLimit-Remaining');
                var message = data.responseJSON.message;
                if (remainingRequests == 0){
                    return React.createElement("div", null, 
                        React.createElement("p", {className: "alert alert-danger"}, "Rate limit exceeded: ", message)
                    );
                }
                return React.createElement("div", null, React.createElement("p", {className: "alert alert-danger"}, "The following error occurred when querying the Github API: ", message));
            }
        }
    };

    return GithubErrorHandler;
});

